@media screen and (min-width:1109px)  {
    .TableHolder{
        display: flex;
        justify-content: center;
        flex: 1;
    }
    table{
        margin-top: 5rem;
        border-collapse: collapse;
        min-width: 70%;
        overflow: hidden;
        background-color: rgb(13, 47, 58);
        border-radius: 10px;
        color: white;
    }
    tr{
        border-bottom: 1px solid rgb(15, 121, 153);
    }
    tr:active{
        background-color: rgb(19, 117, 146);
    }
    th{
        position: sticky;
        top: 0;
        background-color: rgb(11, 86, 109);
    }
    td, th{
        text-align: left;
        height: 2rem;
    }
    td{
        cursor: pointer;
    }
}

@media screen and (max-width:1110px)  {
    .TableHolder{
        display: flex;
        justify-content: center;
        flex: 1;
    }
    table{
        margin-top: 3rem;
        border-collapse: collapse;
        min-width: 97%;
        overflow: hidden;
        background-color: rgb(13, 47, 58);
        border-radius: 7px;
        color: white;
        user-select: none;
    }
    th{
        position: sticky;
        top: 0;
    }
    tr:active{
        background-color: rgb(19, 117, 146);
    }
    td, th{
        text-align: left;
        height: 2rem;
    }
    p{
        max-width: 6rem;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
    
}

/* Common  */

.loaderHolder{
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.432);
    z-index: 0;
}
.circleLoader{
    height: 6rem;
    width: 6rem;
    border: 5px dotted white;
    border-radius: 50%;
    animation: rotater 5s linear infinite ;
}
@keyframes rotater {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}